.payment {
  background-color: white;
}
.paymentContainer > h1 {
  text-align: center;
  padding: 10px;
  font-weight: 400;
  background-color: rgb(234, 237, 237);
  border-bottom: 1px solid lightgray;
}
.paymentContainer > h1 a {
  text-decoration: none;
}
.paymentSection {
  display: flex;
  padding: 20px;
  margin: 0 20px;
  border-bottom: 1px solid lightgray;
}
.paymentTitle {
  flex: 0.2;
}
.payment_adress,
.paymentDetails,
.paymentItems {
  flex: 0.8;
}
.product {
  border-bottom: 1px solid lightgray;
}
